import Component from 'flarum/common/Component';
import FieldSet from 'flarum/components/FieldSet';
import Button from 'flarum/components/Button';
import saveSettings from 'flarum/utils/saveSettings';
import Stream from 'flarum/utils/Stream';
export default class ThirdLinksPage extends Component {
  oninit(vnode) {
    super.oninit(vnode);
    this.saving = false;

    // 6 bağlantı bölümü için alanlar oluştur
    this.sections = Array.from({
      length: 6
    }, (_, i) => ({
      titleKey: "modern-footer.text-".concat(i + 13),
      urlKey: "modern-footer.link-".concat(i + 13)
    }));
    this.values = {};
    const settings = app.data.settings;
    this.sections.forEach(section => {
      this.values[section.titleKey] = Stream(settings[section.titleKey] || '');
      this.values[section.urlKey] = Stream(settings[section.urlKey] || '');
    });
    this.translationPrefix = 'huseyinfiliz-modern-footer.admin.settings.';
  }
  view() {
    const t = key => app.translator.trans(this.translationPrefix + key);
    return m("form", {
      onsubmit: this.onsubmit.bind(this)
    }, m("div", {
      className: "container"
    }, this.sections.map((section, index) => {
      return FieldSet.component({
        label: "".concat(t('link'), " #").concat(index + 1)
      }, [m("div", {
        className: "LinkSection"
      }, m("div", {
        className: "Form-group"
      }, m("label", null, t('text')), m("input", {
        className: "FormControl",
        bidi: this.values[section.titleKey],
        placeholder: t('text')
      })), m("div", {
        className: "Form-group"
      }, m("label", null, t('link')), m("input", {
        className: "FormControl",
        bidi: this.values[section.urlKey],
        placeholder: t('link')
      })))]);
    }), m("div", {
      className: "Form-group"
    }, Button.component({
      type: 'submit',
      className: 'Button Button--primary',
      loading: this.saving
    }, app.translator.trans('core.admin.settings.submit_button')))));
  }
  onsubmit(e) {
    e.preventDefault();
    if (this.saving) return;
    this.saving = true;
    const settings = {};
    this.sections.forEach(section => {
      settings[section.titleKey] = this.values[section.titleKey]();
      settings[section.urlKey] = this.values[section.urlKey]();
    });
    saveSettings(settings).then(() => {
      app.alerts.show({
        type: 'success'
      }, app.translator.trans('core.admin.settings.saved_message'));
    }).catch(() => {}).finally(() => {
      this.saving = false;
      m.redraw();
    });
  }
}
flarum.reg.add('huseyinfiliz-modern-footer', 'admin/Pages/ThirdLinksPage', ThirdLinksPage);