import Component from 'flarum/common/Component';
import FieldSet from 'flarum/components/FieldSet';
import Button from 'flarum/components/Button';
import saveSettings from 'flarum/utils/saveSettings';
import Stream from 'flarum/utils/Stream';
export default class BottomPage extends Component {
  oninit(vnode) {
    super.oninit(vnode);
    this.saving = false;
    this.fields = ['modern-footer.copyright', 'modern-footer.html'];
    this.values = {};
    const settings = app.data.settings;
    this.fields.forEach(key => this.values[key] = Stream(settings[key] || ''));
    this.translationPrefix = 'huseyinfiliz-modern-footer.admin.settings.';
  }
  view() {
    const t = key => app.translator.trans(this.translationPrefix + key);
    return m("form", {
      onsubmit: this.onsubmit.bind(this)
    }, m("div", {
      className: "container"
    }, FieldSet.component({
      label: t('custom_html')
    }, [m("div", {
      className: "Form-group"
    }, m("textarea", {
      className: "FormControl",
      rows: "10",
      bidi: this.values['modern-footer.html'],
      placeholder: t('custom_html')
    }))]), FieldSet.component({
      label: t('bottom_section')
    }, [m("div", {
      className: "Form-group"
    }, m("label", null, t('text')), m("input", {
      className: "FormControl",
      bidi: this.values['modern-footer.copyright']
    }), m("p", {
      className: "helpText"
    }, t('bottom_help')))]), m("div", {
      className: "Form-group"
    }, Button.component({
      type: 'submit',
      className: 'Button Button--primary',
      loading: this.saving
    }, app.translator.trans('core.admin.settings.submit_button')))));
  }
  onsubmit(e) {
    e.preventDefault();
    if (this.saving) return;
    this.saving = true;
    const settings = {};
    this.fields.forEach(key => {
      settings[key] = this.values[key]();
    });
    saveSettings(settings).then(() => {
      app.alerts.show({
        type: 'success'
      }, app.translator.trans('core.admin.settings.saved_message'));
    }).catch(() => {}).finally(() => {
      this.saving = false;
      m.redraw();
    });
  }
}
flarum.reg.add('huseyinfiliz-modern-footer', 'admin/Pages/BottomPage', BottomPage);